.home-box{
    padding:0 20px;
/* image */
     background-image:
        linear-gradient(to bottom,rgba(0,0,0,0.7),rgba(0,0,0,0.7)),
        url("../../public/data/images/bg/bg.jpg")
    ;
    background-color:rgba(63, 63, 63, 0.7);
    background-repeat: no-repeat;
    background-position:center;
    background-size:cover;
    background-attachment: fixed;
    padding-bottom:80px;
}

.home-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    height:80vh;
    text-align: center;
    /* transform:translateY(20%); */
    padding-top:25vh;
}
.home-title{
    font-size: 3.5rem;
    text-align: center;
    font-weight: 700;
    color:white;
    margin-bottom: 10px;
    text-transform: capitalize;
}

