.home-text{
    color:rgb(106, 155, 255);
    font-size: 1rem;
    margin-top: 10px;
    font-weight: 500;
    transition:color 0.3s;
}

.home-bts{
    margin-top:40px;
    width: 220px;
    display: flex;
    justify-content: space-between;
}
.home-attend-bt, .home-submit-bt{
    border: none;
    width:100px;
    height:44px;
    border-radius: 0.2rem;
    line-height:40px;
    cursor: pointer;
    transition:background-color 0.3s;
}
.home-attend-bt{
    font-weight:700;
    font-size: 1rem;
    color:black;
    background-color:white;
}
.home-attend-bt:hover{
    background-color: rgb(225, 225, 225);
}

.home-submit-bt{
    font-weight:700;
    font-size: 1rem;
    color:white;
    background-color:rgb(97,149,255);
}
.home-submit-bt:hover{
    background-color:rgb(74, 114, 193);
}